/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

body {
  background-color: #121212;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

/* Navbar Styles */
.navbar {
  width: 100%;
  background-color: #1e1e1e;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  color: white;
  text-decoration: none;
  padding: 10px 15px;
}

.navbar a:hover {
  background-color: #333;
  border-radius: 5px;
}

/* Authentication Forms */
.auth-container {
  background-color: #222;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.1);
  width: 300px;
  text-align: center;
}

.auth-container h2 {
  margin-bottom: 15px;
}

.auth-container input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
}

.auth-container button {
  width: 100%;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.auth-container button:hover {
  background-color: #45a049;
}

/* Dashboard Styles */
.dashboard-container {
  text-align: center;
  padding: 20px;
}

.transaction-container, .balance-container {
  background-color: #333;
  padding: 15px;
  border-radius: 8px;
  margin-top: 15px;
}

button {
  padding: 10px 15px;
  border: none;
  background-color: #007BFF;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}


body {
  background-color: #121212;
  color: white;
  font-family: Arial, sans-serif;
  text-align: center;
}

.container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
